import * as React from "react";
import banner1 from "/static/img/banner/Banner_002.jpg";
import banner2 from "/static/img/banner/Banner_001.png";
// import "./slide.css";
import left_icon from './ic_common_arrow_left.png'
import right_icon from './ic_common_arrow_right.png'
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import classnames from 'classnames';

// Import Swiper React components
import { Navigation, Pagination, Scrollbar, A11y } from "swiper";
import { Swiper, SwiperSlide } from "swiper/react";
const ReactDOM = require('react-dom');
const Carousel = require('react-responsive-carousel').Carousel;


import { Link } from "gatsby";

const bannerimgList = [banner1, banner2];
const bannerTextList = [
  {
    title: "Dev Dive 2022   ",
    content:
      "             .",
  },
  {
    title: "  ",
    content: "     .",
  },
];

const bannerLinkList = [

  "https://www.youtube.com/playlist?list=PLOdBZFDkhfV00trSo0SEdHMTaLruDNTzV",
  "#",
];

const swiperSlideList = () => {
  const result: any = [];

  for (let i = 0; i < bannerimgList.length; i++) {
    result.push(
      bannerLinkList[i] !== '#' ?

        (<Link to={bannerLinkList[i]}>

          <img
            src={bannerimgList[i]}
            width="100%"
            height="300px"
            className="rounded-3xl"
            style={{ objectFit: "cover", height: "300px" }}
          />
          <div
            className="
              absolute 
              top-1/2	
              left-1/2	
              banner-text
              text-center	
              text-white
              px-[20px]
              w-full
              md:w-[65%]
              

  "
          >
            <p className="inline-block font-bold	text-2xl mr-[7px]">
              {bannerTextList[i].title}
            </p>

            <span className="block text-lg  m-auto mt-2">
              {bannerTextList[i].content}
            </span>
          </div>

        </Link>) :
        <div>

          <img
            src={bannerimgList[i]}
            width="100%"
            height="300px"
            className="rounded-3xl"
            style={{ objectFit: "cover", height: "300px" }}
          />
          <div
            className="
                  absolute 
                  top-1/2	
                  left-1/2	
                  banner-text
                  text-center	
                  text-white
                  px-[20px]
                  w-full
                  md:w-[65%]
    
      "
          >
            <p className="inline-block font-bold	text-2xl mr-[7px]">
              {bannerTextList[i].title}
            </p>

            <span className="block text-lg  m-auto mt-2">
              {bannerTextList[i].content}
            </span>
          </div>
        </div>


    );
  }
  return [...result];
};
const renderIndicator = (
  clickHandler: (e: React.MouseEvent | React.KeyboardEvent) => void,
  isSelected: boolean,
  index: number,
  label: string
) => <li onClick={clickHandler} role="button" className={classnames("mx-[6px] mb-[20px] inline-block w-[6px] h-[6px] bg-white rounded-full", { "opacity-40": !isSelected })}></li>;
const renderArrowPrev = (clickHandler: () => void, hasPrev: boolean, label: string) => hasPrev && (
  <button onClick={clickHandler}
    className="
    absolute 
    bottom-1/2 
    top-1/2  
    left-0
    hidden
    pl-[20px]
    md:inline-block
    md:pl-[40px]
    xl:left-1/2  
    xl:translate-x-[-500px]
    xl:pl-0
    z-10
  ">
    <img
      className="max-w-[18px] "
      src={left_icon} />
  </button>
)
const renderArrowNext = (clickHandler: () => void, hasNext: boolean, label: string) => hasNext && (
  <button
    onClick={clickHandler}
    className="
    absolute 
    bottom-1/2 
    top-1/2  
    right-0
    hidden
    pr-[20px]
    md:inline-block
    md:pr-[40px]
    xl:right-1/2  
    xl:translate-x-[496px]
    xl:pr-0
    z-10">
    <img
      className="max-w-[18px]  "
      src={right_icon} />
  </button>
)

const TopBanner = () => {
  return (
    <div className="demo-carousel relative max-w-[1920px] mx-auto">

      <Carousel showArrows={true} autoPlay={true} infiniteLoop={true} interval={4000}
        renderIndicator={renderIndicator}
        renderArrowPrev={renderArrowPrev}
        renderArrowNext={renderArrowNext}
        showStatus={false}
      >
        {swiperSlideList().map((item, index) => (
          <div key={index}>{item}</div>
        ))}

      </Carousel >
    </div >


  )
};

export default TopBanner;
