// import { visuallyHidden } from "../styles/utils"
// @ts-ignore

import Hero from "../texts/hero";
import TopBanner from "./banner/top-banner";
// @ts-ignore
const cateNameList = {
  engineering: "",
  data: "",
  design: "",
  culture: "",
};

type PostsProps = {
  posts: {
    title: string
    date: string
    slug: string
    author: string
    email: string
    category: string
    description: string
    tags?: {
      name: string
      slug: string
    }[]
    index: number
    body: string
  }[];
};
const Homepage = ({ posts }: PostsProps) => {

  if (typeof window !== "undefined") {
    const location = window.location;

    let category = "engineering";
    if (location && "search" in location) {
      if (location.search.includes("category")) {
        category = location.search.split("?")[1].split("category=")[1];
      }
    }

    return (
      <Layout selectedCategory={category}>
        <div className="">
          <div className="pt-5 px-auto mx-5">
            <TopBanner />
          </div>
          <div className="w-full  px-5 md:px-10">
            {
              <div className="mt-[80px] md:max-w-md lg:max-w-lg mx-auto">
                <Listing
                  posts={posts}
                  showTags={false}
                  selectedCategory={category}
                // {category}
                />
              </div>

            }
          </div>
        </div>
      </Layout>
    );
  } else {
    return null;
  }
};

export default Homepage;
